import React from 'react';
import Section from "./components/Section";
import {lightGradient} from "./Helpers";
import {MD_BREAKPOINT} from "../../../Constants/Constants";
import Media from "react-media";

class Internships extends Section {

	constructor(props) {
		super(props);
		//console.log("About Constructor", props);
		this.aboutRef = React.createRef();
	}

	componentDidMount() {
		this.props.updateRef(this.props.title, this.aboutRef);
	}

	styles = {
		...Section.styles,
		mainContainer: {
			minHeight: "auto",
			...Section.styles.mainContainer,
			// background: "url(" + IMAGES_DIR + "/home.jpg)",
			// backgroundSize: "cover",
			backgroundImage: (this.props.backgroundColor ? lightGradient(this.props.backgroundColor) : "#fff"),
		},
		contentContainer: {
			...Section.styles.contentContainer,
			padding: "auto",
			// minHeight: "100vh",
			display: "flex",
			flexDirection: "column",
			// alignContent: "center",
			justifyContent: "center",
			// alignItems: "center",
			fontSize: "4.5vmin"
		},
		title: {
			...Section.styles.title,
			// fontFamily: "Yanone Kaffeesatz, sans-serif",
			fontStyle: "bold",
			textAlign: "center"
		},
		subTitle: {
			...Section.styles.subTitle,
			// fontFamily: "Abel, sans-serif",
			maxWidth: "90%",
			fontSize: "5vmin",
			lineHeight: "6vmin",
			// color: "#ffc107",
			// padding: "0.2em",
			// backgroundColor: "rgba(0,0,0,0.8)"
		},
	};

	render() {
		return (
			<div style={this.styles.mainContainer} ref={this.aboutRef}>
				<div style={this.styles.contentContainer}>
					<h1 style={this.styles.title}>{this.props.title}</h1>
					{this.props.subtitle ? <h2 style={this.styles.subTitle}>{this.props.subtitle}</h2> : null}
					{/*<Carousel items={this.props.data}/>*/}
					{
						this.props.data.map((item, index) => {
							return (
								<CommonListItem key={index} styles={this.styles} {...item} renderDate={true}/>
							)
						})
					}
				</div>
			</div>
		);
	}
}

export const CommonListItem = (props) => {
	//console.log(props);
	const title = <div key="title" style={props.styles.subTitle}>{props.title}</div>;
	const duration = props.startDate?
		(<div key="duration">{props.startDate + " - " + props.endDate}</div>)
		:
		(<div key="duration">{props.endDate}</div>);
	const subtitle = <div key="subtitle" style={{fontStyle: 'italic'}}>{props.subTitle}</div>;
	const description = <div key="description">{props.description}</div>;

	return (
		<div style={{...props.styles.contentContainer, paddingBottom: "10vmin"}}>
			<Media query={`max-width: ${MD_BREAKPOINT}`}>
				{
					matches => matches ?
						(
							<div style={{display: 'flex', flexDirection: "column"}}>
								{[title, duration, subtitle, description]}
							</div>
						)
						:
						(
							<div style={{display: 'flex', flexDirection: "column"}}>
								<div style={{display: 'flex', flexDirection: "row", justifyContent: "space-between"}}>
									{[title, duration]}
								</div>
								{[subtitle, description]}
							</div>
						)
				}
			</Media>
		</div>
	);
};

export default Internships;