import React, {Component} from 'react';
import {Col, Container, Row} from 'reactstrap';
import Skills from './components/Skills';
import Section from "./components/Section";
import Intro from './components/Intro';
import * as Constants from '../../Constants/Constants';
import {resume_download_link} from '../../Constants/Constants';
import {data} from '../../store/data';
import QRCode from 'qrcode.react';

class Paper extends Component {

	//border: "5px solid #607d8b"
	styles = {
		container: {
			align: "center",
			width: "100%",
			border: "5px double #555",
		},
		paper: {
			padding: "10px 10px",
//			margin: "10px 10px",
			//minHeight: "100vh",
			// border: "5px double #555",
			// borderRadius: "5px",
			// color: "#311B92",
			backgroundColor: "#fff",
		},
		title: {
			fontWeight: 500,
		},
		designation: {
			fontWeight: 400,
			fontStyle: 'italic'
		},
		keyValue: {
			padding: "0",
			margin: "0"
		},
		sidebar: {
			padding: "0em 1em",
			borderTopRightRadius: "0",
			borderBottomRightRadius: "0",
			borderRadius: "5px",
			borderRight: "1px solid #aaa"
			//backgroundColor: "#4527A0",
			//color: "#fff",
		},
		'rightSidebar': {
			padding: "0em 1em"
		},
		sidebarLink: {
			display: "none"
			// color: "#7E57C2"
			// color: "#E8F5E9"
		},
		link: {
			display: "none"
			// color: "#7E57C2"
		},
		social_links: {
			color: "#000"
		},
		socialLinksContainer: {
			backgroundImage: "linear-gradient(to right, #ddd, #ccc)",
			// backgroundColor: "#ccc",
			padding: "20px 10px"
		},

		section: {
			padding: "15px 10px",
			item: {
				paddingBottom: "0.5em",
				fontSize: "1rem",
				lineHeight: "1.5rem",
				fontWeight: 400,
				title: {fontWeight: 500, lineHeight: "1.5em"},
				subTitle: {fontStyle: "italic"},
				description: {},
				duration: {fontWeight: 500},
			}
		}
	};

	// componentDidMount() {
	// 	this.updateWindowDimensions();
	// 	window.addEventListener('resize', this.updateWindowDimensions);
	// }
	//
	// componentWillUnmount() {
	// 	window.removeEventListener('resize', this.updateWindowDimensions);
	// }
	//
	// updateWindowDimensions() {
	// 	this.setState({ window_width: window.innerWidth, window_height: window.innerHeight });
	// 	//console.log(window.innerWidth);
	// }

	constructor(props) {
		super(props);
		this.state = {
			data: data,
			// window_width: 0,
			// window_height: 0
		};
		// this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
	}

	render() {

		if (this.props.window_width < 1024) {
			return (
				<div>
					<div style={{minHeight: "100vh"}}
							 className="d-flex flex-column justify-content-center align-items-stretch text-center">
						<p>This Section is visible only on Large Screens.</p>
						<div>Click <a href={resume_download_link}>Here</a> to Download</div>
					</div>
				</div>
			);
		}

		const data = this.state.data;
		let kvs = data.kvs;
		let skills = data.skills;
		let education = data.education;
		let internships = data.internships;
		let projects = data.projects;
		let courses = data.courses;

		return (
			<Container>
				<div style={this.styles.container}>
					<div style={this.styles.paper}>
						<Row>
							<Col>
								<div style={{position: 'absolute', right: "20px", zIndex: "1000", backgroundColor: "#fff"}}>
									<div className="d-flex flex-column justify-content-between align-items-center"
											 style={{padding: "2px 2px", margin: "10px"}}>
										<QRCode size={100} value='https://lalitjain98.github.io/my-resume/mini'
														style={{padding: "2px 2px", backgroundColor: 'white'}}/>
										<p className="my-1 text-center">View CV Online</p>
									</div>
								</div>
								<Intro
									title={data.title}
									subtitle={data.subtitle}
									styles={this.styles}
									data={kvs}
									printSocialAnchors
									social={data.social}
								/>
							</Col>
						</Row>

						<Row style={{marginTop: "1em"}}>
							<Col xs={5} style={this.styles.sidebar}>
								<Section
									styles={this.styles}
									// icon="fa fa-graduation-cap"
									title="Education"
									data={education}
									datePosition={Constants.DATE_IN_COLUMN}
									dateFormat={Constants.END_DATE}
									showDescription={true}
									linkStyles={this.styles.link}/>
								<Skills
									styles={this.styles}
									commaSeparated
									// icon="fa fa-code"
									data={skills}
								/>
								<Section
									styles={this.styles}
									// icon="fa fa-laptop-code"
									title="Courses and Certificates"
									data={courses}
									datePosition={Constants.DATE_INLINE}
									dateFormat={Constants.END_DATE}
									showDescription={false}
									linkStyles={this.styles.sidebarLink}/>
							</Col>

							{/*fas fa-code */}
							<Col xs={7} style={this.styles.rightSidebar}>

								<Section
									styles={this.styles}
									// icon="fa fa-suitcase"
									title="Past Internships"
									data={internships}
									datePosition={Constants.DATE_IN_COLUMN}
									dateFormat={Constants.END_DATE}
									showDescription={true}
									linkStyles={this.styles.link}/>
								<Section
									styles={this.styles}
									// icon="fa fa-file-code"
									title="Projects"
									data={projects}
									datePosition={Constants.DATE_IN_COLUMN}
									dateFormat={Constants.END_DATE}
									showDescription={true}
									linkStyles={this.styles.link}/>
							</Col>
						</Row>
						{/*<Row>*/}
						{/*<Col>*/}
						{/*<SkillsComponent*/}
						{/*commaSeparated*/}
						{/*// icon="fa fa-code"*/}
						{/*data={skills}*/}
						{/*/>		*/}
						{/*</Col>*/}
						{/*</Row>*/}
					</div>
				</div>
			</Container>
		);
	}
}

export default Paper;