import React from 'react';
import {Col, Row} from 'reactstrap';
import Contacts from "./Contacts";


const Intro = ({title, subtitle, showQR, qrcode, styles, data, social, printSocialAnchors, socialLinkSize}) => {
	// //console.log(social);

	return (
		<div style={styles.section}>
			<Row>
				<Col>
					<div className="d-flex flex-column flex-lg-row align-items-lg-center justify-content-lg-between">
						<div>
							<h1 style={styles.title}>{title}</h1>
							<h4 style={styles.designation}>{subtitle}</h4>
						</div>
						{/*{*/}
						{/*showQR ?(*/}
						{/*<div className="qrcode-container">*/}
						{/*<div className="d-flex flex-column justify-content-between align-items-center" style={{padding: "2px 2px"}}>*/}
						{/*<QRCode size={100} value={qrcode} style={{padding: "2px 2px", backgroundColor: 'white'}}/>*/}
						{/*<p className="my-1 text-center">View CV Online</p>*/}
						{/*</div>*/}
						{/*</div>*/}
						{/*):null*/}
						{/*}*/}
					</div>
					<Contacts data={data} styles={styles} social={social} printSocialAnchors={printSocialAnchors}
										socialLinkSize={socialLinkSize}/>
				</Col>
			</Row>
		</div>
	);
};

