import React from 'react';
import Section from "./components/Section";
import {lightGradient} from "./Helpers";
import {IMAGES_DIR, LG_BREAKPOINT, resume_download_link} from "../../../Constants/Constants";
import posed from "react-pose";

class About extends Section {

	constructor(props) {
		super(props);
		//console.log("About Constructor", props);
		this.aboutRef = React.createRef();
		this.state = {
			aboutMe: "I am a pre-final year Computer Science Student at University School of ICT, GGS Indraprastha University, New Delhi.\n" +
				"I am passionate about coding and love to develop quality apps. My personal favorite programming languages are Python and C++.\n" +
				"My hobbies include reading books and listening to music.\n" +
				"Also, I love F.R.I.E.N.D.S and Game of Thrones."
		}
	}

	componentDidMount() {
		this.props.updateRef(this.props.title, this.aboutRef);
	}

	styles = {
		...Section.styles,
		mainContainer: {
			...Section.styles.mainContainer,
			// background: "url(" + IMAGES_DIR + "/home.jpg)",
			// backgroundSize: "cover",
			backgroundImage: (this.props.backgroundColor ? lightGradient(this.props.backgroundColor) : "#fff"),
			// padding: "10vmin 10vmin 0 10vmin",
		},
		contentContainer: {
			...Section.styles.contentContainer,
			padding: "auto",
			minHeight: "100vh",
			display: "flex",
			flexDirection: "column",
			// alignContent: "center",
			justifyContent: "flex-start",
			alignItems: "center",
			fontSize: "4.5vmin",
			lineHeight: "6.5vmin"
		},
		title: {
			...Section.styles.title,
			// fontFamily: "Yanone Kaffeesatz, sans-serif",
			fontStyle: "bold",
		},
		subTitle: {
			...Section.styles.subTitle,
			fontFamily: "Abel, sans-serif",
			maxWidth: "90%",
			color: "#ffc107",
			padding: "0.2em",
			// backgroundColor: "rgba(0,0,0,0.8)"
		},
	};

	render() {
		return (
			<div style={this.styles.mainContainer} ref={this.aboutRef}>
				<div style={this.styles.contentContainer}>
					<h1 style={this.styles.title}>{this.props.title}</h1>
					{this.props.subtitle ? <h2 style={this.styles.subTitle}>{this.props.subtitle}</h2> : null}
					<div style={this.props.window_width >= LG_BREAKPOINT ?
						{display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center"} :
						{display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center"}}>
						<img alt="Avatar" src={IMAGES_DIR + "/avatar.jpg"}
								 style={{
									 alignSelf: "center",
									 margin: "1em",
									 height: "30vmin",
									 width: "30vmin",
									 border: "5px solid white",
									 borderRadius: "10em"
								 }}/>
						<div style={{padding: "1em"}}>
							<div>
								{
									this.state.aboutMe.split("\n").map((item, index) => <p key={index}>{item}</p>)
								}
							</div>
						</div>
					</div>


					<div style={{display: 'flex', justifyContent: 'center', marginBottom: "2em"}}>
						<ScaleUpOnHover>
						<a className="text-decoration-none text-dark btn btn-outline-success btn-lg bold"  href={resume_download_link}><span className="fa fa-download m-1"></span>{"Download My Resume"}</a>
						</ScaleUpOnHover>
					</div>


				</div>
			</div>
		);
	}
}

const ScaleUpOnHover = posed.div({
	hoverable: true,
	init: {scale: 1},
	hover: {scale: 1.1},
	pressable: true,
	press: { scale: 0.9 }
});
export default About;