import React from 'react';
import Section from "./components/Section";
import {lightGradient} from "./Helpers";
import Media from "react-media";
import {MD_BREAKPOINT} from "../../../Constants/Constants";
import Posed from 'react-pose';
import Carousel from "./components/Carousel";
class Projects extends Section {

	constructor(props) {
		super(props);
		//console.log("About Constructor", props);
		this.aboutRef = React.createRef();
	}

	componentDidMount() {
		this.props.updateRef(this.props.title, this.aboutRef);
	}

	styles = {
		...Section.styles,
		mainContainer: {
			...Section.styles.mainContainer,
			// minHeight: "auto",
			// background: "url(" + IMAGES_DIR + "/home.jpg)",
			// backgroundSize: "cover",
			backgroundImage: (this.props.backgroundColor ? lightGradient(this.props.backgroundColor) : "#fff"),
		},
		contentContainer: {
			...Section.styles.contentContainer,
			padding: "auto",
			// minHeight: "100vh",
			display: "flex",
			flexDirection: "column",
			// alignContent: "center",
			justifyContent: "space-between",
			// alignItems: "center",
			fontSize: "4.5vmin"
		},
		title: {
			...Section.styles.title,
			// fontFamily: "Yanone Kaffeesatz, sans-serif",
			fontStyle: "bold",
			textAlign: "center"
		},
		subTitle: {
			...Section.styles.subTitle,
			fontFamily: "Abel, sans-serif",
			maxWidth: "90%",
			fontSize: "5vmin",
			lineHeight: "6vmin",
			// color: "#ffc107",
			// padding: "0.2em",
			// backgroundColor: "rgba(0,0,0,0.8)"
		},
		listItem: {
			...Section.styles.listItem,

		}
	};

	render() {
		return (
			<div style={{...this.styles.mainContainer, minHeight:(this.props.window_width > MD_BREAKPOINT ? "100vh" : "auto")}} ref={this.aboutRef}>
				<div style={{...this.styles.contentContainer}}>
					<h1 style={this.styles.title}>{this.props.title}</h1>
					{this.props.subtitle ? <h2 style={this.styles.subTitle}>{this.props.subtitle}</h2> : null}
					{/*<div style={{maxWidth:"50vw", padding:"1em"}}>*/}
					<Carousel items={this.props.data} styles={this.styles}/>
					{/*{*/}
					{/*	this.props.data.map((item, index) => <CommonListItem key={index} styles={this.styles} {...item}/>)*/}
					{/*}*/}

					{/*</div>*/}
				</div>
			</div>
		);
	}
}

export const ListItemPose = Posed.div({
	enter: { y: 0, opacity: 1 },
	exit: { y: 50, opacity: 0 },
	transition: {
		type: 'spring'
	}
});

