import React, {Component} from 'react';
import {Col, Container, Row} from 'reactstrap';
import Skills from './components/Skills';
import Section from "./components/Section";
import Intro from './components/Intro';
import * as Constants from '../../Constants/Constants';
import {data} from '../../store/data';

class FullPage extends Component {

	constructor(props) {
		super(props);
		this.state = {
			data: data
		}
	}

	//border: "5px solid #607d8b"
	styles = {
		paper: {
			padding: "0px 10px",
			margin: "10px 10px",
			border: "1px solid #311B92",
			borderRadius: "5px",
			color: "#311B92",
			backgroundColor: "#fff",
		},
		title: {
			fontWeight: 500,
		},
		designation: {
			fontWeight: 400,
			fontStyle: 'italic'
		},
		keyValue: {
			padding: "0",
			margin: "0"
		},
		sidebar: {
			padding: "1em",
			borderTopRightRadius: "0",
			borderBottomRightRadius: "0",
			borderTopLeftRadius: "5px",
			borderBottomLeftRadius: "5px",
			// borderRadius: "5px",
			backgroundColor: "#4527A0",
			color: "#fff",
		},
		'rightSidebar': {
			padding: "1em"
		},
		sidebarLink: {
			color: "#E8F5E9"
		},
		link: {
			color: "#7E57C2"
		},
		social_links: {
			color: "#fff"
		},
		section: {
			paddingBottom: "1em",
			item: {
				paddingBottom: "0.1em",
				fontSize: "1rem",
				fontWeight: 400,
				title: {fontWeight: 500, lineHeight: "1.5em"},
				subTitle: {fontStyle: "italic"},
				description: {},
				duration: {fontWeight: 500},
			}
		}
	};

	render() {
		const data = this.state.data;
		let kvs = data.kvs;
		let social = data.social;
		let skills = data.skills;
		let education = data.education;
		let internships = data.internships;
		let projects = data.projects;
		let courses = data.courses;

		return (
			<Container style={{margin: "1em auto"}}>
				<div style={this.styles.paper}>
					<Row>
						<Col md={5} lg={4} style={this.styles.sidebar}>
							<Intro
								title={data.title}
								subtitle={data.subtitle}
								// showQR
								qrcode="https://lalitjain98.github.io/my-resume"
								styles={this.styles}
								data={kvs}
								social={social}
							/>
							<Skills
								containerStyle={{paddingTop: "1em"}}
								asList
								styles={this.styles}
								icon="fa fa-code"
								data={skills}
							/>
							<Section
								styles={this.styles}
								title="Courses and Certificates"
								data={courses}
								datePosition={Constants.DATE_INLINE}
								dateFormat={Constants.END_DATE}
								showDescription={true}
								linkStyles={this.styles.sidebarLink}/>
						</Col>

						{/*fas fa-code */}
						<Col style={this.styles.rightSidebar}>
							<Section
								styles={this.styles}
								title="Education"
								data={education}
								datePosition={Constants.DATE_IN_COLUMN}
								dateFormat={Constants.END_DATE}
								showDescription={true}
								linkStyles={this.styles.link}/>
							<Section
								styles={this.styles}
								title="Past Internships"
								data={internships}
								datePosition={Constants.DATE_IN_COLUMN}
								dateFormat={Constants.END_DATE}
								showDescription={true}
								linkStyles={this.styles.link}/>
							<Section
								styles={this.styles}
								title="Projects"
								data={projects}
								// linkPosition={SUBTITLE_LINK_INLINE}
								datePosition={Constants.DATE_IN_COLUMN}
								dateFormat={Constants.END_DATE}
								showDescription={true}
								linkStyles={this.styles.link}/>
						</Col>
					</Row>
				</div>
			</Container>
		);
	}
}

export default FullPage;