import React from 'react';
import {Col, Row} from 'reactstrap';
import {LinkComponent} from '../Helpers';
import * as Constants from '../../../Constants/Constants';
import {SUBTITLE_LINK_INLINE} from '../../../Constants/Constants';

const DurationComponent = ({startDate, endDate, styles}) => {
	// //console.log("Date Styles", styles);
	return (
		<div style={styles || null}>
			{startDate} {startDate ? " - " : null} {endDate}
		</div>
	);
};

const TitleComponent = ({title, styles}) => {
	// //console.log("Title Styles", styles);
	return (
		<div style={styles || null}>
			{title}
		</div>
	);
};

const SubTitleComponent = ({subTitle, styles}) => {
	// //console.log("Subtitle Styles", styles);
	return (
		<div style={styles || null}>
			{subTitle}
		</div>
	);
};


const DescriptionComponent = ({description, styles}) => {
	// //console.log("Description Styles", styles);
	description = description ? description.split("\n") : [];
	return (
		<div style={styles || null}>
			{description.map((item, index) => <div key={index}>{item}</div>)}
		</div>
	);
};


export const ExperienceItemComponent = ({styles, data, dateFormat, datePosition, linkStyles, showDescription, paddingBottom, linkPosition}) => {

	let node = null;

	const description = showDescription ?
		<DescriptionComponent description={data.description}
													styles={!styles ? null : styles.section.item.description || null}/> : null;
	const title = <TitleComponent title={data.title} styles={!styles ? null : styles.section.item.title || null}/>;
	const subtitle = data.subTitle ?
		<SubTitleComponent subTitle={data.subTitle} styles={!styles ? null : styles.section.item.subTitle || null}/> : null;
	const certificateLink = data.certificate_link ?
		<LinkComponent link={data.certificate_link} anchor={"View Certificate"}
									 styles={!styles ? null : linkStyles || null}/> : null
	const projectLink = data.project_link ?
		<LinkComponent link={data.project_link} anchor={"Project Link"}
									 styles={!styles ? null : linkStyles || null}/> : null;
	const duration = <DurationComponent endDate={data.endDate}
																			styles={!styles ? null : styles.section.item.duration || null}/>;

	let subtitle_link_description_node = (
		<div>
			{subtitle}
			{description}
			{certificateLink} {projectLink}
		</div>
	);
	if (linkPosition === SUBTITLE_LINK_INLINE) {
		subtitle_link_description_node = (
			<div>
				<div className="d-flex flex-row justify-content-between">
					<div>
						{subtitle}
					</div>
					<div className="text-right">
						{certificateLink} {projectLink}
					</div>
				</div>
				{description}
			</div>
		)
	}


	switch (datePosition) {
		case Constants.NO_DATE:
			node = (
				<Row style={{paddingBottom: {paddingBottom}}}>
					<Col style={{display: 'flex', flexDirection: 'column'}}>
						{title}
						{subtitle_link_description_node}
					</Col>
				</Row>
			);
			break;
		case Constants.DATE_INLINE:
			node = (
				<Row style={{paddingBottom: {paddingBottom}}}>
					<Col style={{display: 'flex', flexDirection: 'column'}}>
						<div style={{display: 'flex', flexDirection: 'row', justifyContent: 'space-between'}}>
							<div>{title}</div>
							<div>{duration}</div>
						</div>
						{subtitle_link_description_node}
					</Col>
				</Row>
			);
			break;
		case Constants.DATE_IN_COLUMN:
			node = (
				<Row style={{paddingBottom: {paddingBottom}}}>
					<Col xs={2} style={{padding: "0", margin: "0", marginLeft: "1em"}} className="no-gutters">
						{duration}
					</Col>
					<Col style={{display: 'flex', flexDirection: 'column'}} className="no-gutters">
						{title}
						{subtitle_link_description_node}
					</Col>
				</Row>
			);
			break;
		//case Constants.DATE_IN_ROW:
		default:
			node = (
				<Row style={{paddingBottom: {paddingBottom}}}>
					<Col style={{display: 'flex', flexDirection: 'column'}}>
						{title}
						{duration}
						{subtitle_link_description_node}
					</Col>
				</Row>
			);
			break;
	}

	return (
		<div style={!styles ? null : styles.section.item}>
			{node}
		</div>
	);
};

const Section = ({title, data, styles, datePosition, dateFormat, showDescription, linkStyles, icon, linkPosition}) => {
	return (
		<div style={!styles ? null : styles.section}>
			<h3 style={{paddingBottom: '0.25em', borderBottom: "1px solid #aaa"}}>
				{title}<span className={icon} style={{padding: "0.2em"}}></span>
			</h3>
			<div>
				{data.map((item, index) => <ExperienceItemComponent styles={styles || null} data={item} key={index}
																														datePosition={datePosition}
																														dateFormat={dateFormat} showDescription={showDescription}
																														linkStyles={linkStyles}
																														linkPosition={linkPosition}
				/>)}
			</div>
		</div>
	);
};

export default Section;