import React from 'react';
import Section from "./components/Section";
import {lightGradient} from "./Helpers";
import {MD_BREAKPOINT} from "../../../Constants/Constants";
import Media from "react-media";

class Skills extends Section {

	constructor(props) {
		super(props);
		//console.log("About Constructor", props);
		this.aboutRef = React.createRef();
	}

	componentDidMount() {
		this.props.updateRef(this.props.title, this.aboutRef);
	}

	styles = {
		...Section.styles,
		mainContainer: {
			...Section.styles.mainContainer,
			// background: "url(" + IMAGES_DIR + "/home.jpg)",
			// backgroundSize: "cover",
			minHeight: "auto",
			textAlign: "left",
			backgroundImage: (this.props.backgroundColor ? lightGradient(this.props.backgroundColor) : "#fff"),
		},
		contentContainer: {
			...Section.styles.contentContainer,
			padding: "auto",
			minHeight: "100vh",
			display: "flex",
			flexDirection: "column",
			// alignContent: "center",
			// justifyContent: "center",
			// alignItems: "center",
		},
		title: {
			textAlign: "center",
			...Section.styles.title,
			// fontFamily: "Yanone Kaffeesatz, sans-serif",
			fontStyle: "bold",
		},
		subTitle: {
			...Section.styles.subTitle,
			// fontFamily: "Abel, sans-serif",
			maxWidth: "90%",
			// padding: "0.2em",
			textAlign: "left",
			// backgroundColor: "rgba(0,0,0,0.8)"
		},
	};

	render() {
		return (
			<div style={this.styles.mainContainer} ref={this.aboutRef}>
				<div style={this.styles.contentContainer}>
					<h1 style={this.styles.title}>{this.props.title}</h1>
					{this.props.subtitle ? <h2 style={this.styles.subTitle}>{this.props.subtitle}</h2> : null}
					<div style={{display: "flex", flexDirection: "column"}}>
						{
							this.props.data.map((item, index) => <List styles={this.styles} key={index} title={item.title}
																												 data={item.data} window_width={this.props.window_width}/>)
						}
					</div>
				</div>
			</div>
		);
	}
}

export const List = (props) => (
	<div style={{
		display: "flex",
		flexDirection: "column",
		margin: "0.5em",
		// justifyContent: "space-between",
		// alignItems: "center",
	}}>
		<div style={props.styles.subTitle}>{props.title + ": "}</div>
		<Media query={`(max-width: ${MD_BREAKPOINT})`}>
			{
				matches => matches ?
					(
						<ul>
							{props.data.map((item, index) => <li key={index}>{item}</li>)}
						</ul>
					)
					:
					(
						<div>
							{props.data.join(", ")}
						</div>
					)
			}
		</Media>
	</div>
);

export default Skills;