import React from 'react';

class Section extends React.Component {

	constructor(props) {
		super(props);
		//console.log("Section Constructor", props);
	}

	static styles = {
		mainContainer: {
			minHeight: "100vh",
			borderBottom: "2px solid white",
			padding: "10vmin 10vmin 0 10vmin",
		},
		contentContainer: {
			textAlign: "left",
			color: "#555",
			fontFamily: "Farsan, cursive",
			fontSize: "4vmin",
			lineHeight: "6vmin",
			padding: 0,
		},
		title: {
			color: "#444",
			fontFamily: "Nova Round, sans-serif",
			fontWeight: "bold",
			fontSize: "9vmin",
			lineHeight: "11vmin"
		},
		subTitle: {
			color: "#454",
			fontFamily: "Abel, sans-serif",
			fontSize: "6vmin",
			lineHeight: "8vmin",
		},
		listITem: {
			backgroundColor: "rgba(0,0,0,0.5)",
			padding: "1em"
		}
	};

	render() {
		return (
			<div style={this.styles.mainContainer}>
				{
					this.props.children
				}
			</div>
		);
	}
};

export default Section;